
*{
  max-width: 2550px;
}
.main_page_skills{
  position: absolute;
  top: 300px;
  left: 15%;
}


.page_skills{
    display: flex;
    flex-direction: row;
}


.paragraphe_skills{  
    width: 50%;
    color: white;  
}

.wrapper_skills{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 40px;
    gap: 2%;
    /*width: 100%;*/
    
}
  
  .progress {
    background: rgba(255,255,255,0.1);
    justify-content: flex-start;
    border-radius: 100px;
    align-items: center;
    position: relative;
    padding: 0 5px;
    display: flex;
    height: 40px;
    width: 500px;
    
  }
  
  .progress-value {
    animation: load 3s normal forwards;
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #fff;
    height: 30px;
    width: 0;
  }
  
  @keyframes load {
    0% { width: 0; }
    100% { width: 100%; }
  }
  .logo_skills
  {
    width: 6%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    white-space: nowrap;
    gap: 20%;
    margin-right: 8%;
  }
  .icon_skills{
    width: 100%;
    border-radius: 100px;
    position: static;
    border-radius: 100px;
    background-color: white;
    
  }
  .text_skills{
    margin-top: 3%;
    min-width: 14%;
  }

  /* Les barres de progression en fonction des languages de programmation */

  /*progression de sass et react*/

  .progress-value_sass_react {
    animation: load_sass_react 3s normal forwards;
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #fff;
    height: 30px;
    width: 0;
  }
  
  @keyframes load_sass_react{
    100% { width: 70%; }
  }

  /* progression de javascript */


  .progress-value_javascript {
    animation: load_javascript 3s normal forwards;
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #fff;
    height: 30px;
    width: 0;
  }
  
  @keyframes load_javascript{
    100% { width: 85%; }
  }

  /* progression de node.js */


.progress-node {
    animation: load_node 3s normal forwards;
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #fff;
    height: 30px;
    width: 0;
  }
  
  @keyframes load_node{
    100% { width: 60%; }
  }


  
/* bloc de droite */

.second_section_skills{
    border: 1px solid white;
    height: 550px;
    width: 40%;
    color: white;
    font-size: 25px;
    font-style: italic;
    padding-left: 2%;
    padding-right: 2%;
    overflow-y: auto;
}
.div_skills{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.list_second_skills{
  width: 100%;
  height: 10em;
  display: flex;
  flex-direction: column;
justify-content: space-around;
align-items: flex-start;
}

.quote_skills{
  font-size: 1.5em;
    font-style: italic;
    color: white;
    margin-top: 7em;
}

/* Gestion des media queries */

@media only screen and (max-width: 1520px) {
  .page_skills {
      flex-direction: column;
  }
  .paragraphe_skills{
    width: 80%;
  }
  .wrapper_skills{
    justify-content: center;
    width: 100%;
  }
  .second_section_skills{
    margin-top: 10%;
    width: 80%;
  }
  .quote_skills{
    margin-top: 5%;
    width: 80%;
  }
}

@media only screen and (max-width: 1240px) {
  .wrapper_skills {
      justify-content: space-around;
  } 
}

@media only screen and (max-width: 960px) {
  .second_section_skills {
      padding-bottom: 5%;
  } 
  
  .progress{
    width: 50%;
  }
}

@media only screen and (max-width: 768px) {
    .icon_skills {
      display: none;
  } 
}

  